
<template lang="pug">
.commit-component
  template
    el-form-item(label='')
      el-select(v-model='_form.status', placeholder='')
        el-option(label='', value='draft')
        el-option(label='', value='schedule')
        el-option(label='', value='send')
    el-form-item(label='', v-if='_form.status === "schedule"')
      el-date-picker( v-model='_form.sendAt', type='datetime', placeholder='')
    el-button(type='primary', size="large", @click='onSubmit') 
    el-dialog(title='', v-model='dialogVisible')
      .dialog-footer(slot='footer')
        el-button(@click='')  
        el-button(type='primary', @click='dialogConfim')  
</template>

<script>

export default {
  name: 'commit-component',
  computed: {
  },
  props: {
    _form: Object,
    dialogVisible: Boolean,
    dialogConfim: Function,
    onSubmit: Function,
  },
  data () {
    return {
    }
  },
  methods: {
  },
  beforeMount () {
  }
}
</script>




